.analog{
    padding-top: count-rem(40);
    padding-bottom: count-rem(80);
    &__title{
        font-size: count-rem(36);
        font-weight: 600;
    }
    &-slider{
        margin-top: count-rem(26);
        position: relative;
        display: flex;
        &__btn-previe{
            position: absolute;
            top: 50%;
            left: -15px;
            width: 30px;
            height: 30px;
            border-radius: 50%;
            box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
            border: none;
            background-color: #fff;
            cursor: pointer;
            &::after{
                position: absolute;
                top: 12px;
                left: 10px;
                content: '';
                width: 10px;
                height: 3px;
                border-radius: 2px;
                background-color: #383838;
                transform: rotateZ(-45deg);
            }
            &::before{
                position: absolute;
                top: 17px;
                left: 10px;
                content: '';
                width: 10px;
                height: 3px;
                border-radius: 2px;
                background-color: #383838;
                transform: rotateZ(45deg);
            }
        }
        &__btn-next{
            position: absolute;
            top: 50%;
            right: -15px;
            width: 30px;
            height: 30px;
            border-radius: 50%;
            box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
            border: none;
            background-color: #fff;
            cursor: pointer;
            &::after{
                position: absolute;
                top: 12px;
                left: 10px;
                content: '';
                width: 10px;
                height: 3px;
                border-radius: 2px;
                background-color: #383838;
                transform: rotateZ(45deg);
            }
            &::before{
                position: absolute;
                top: 17px;
                left: 10px;
                content: '';
                width: 10px;
                height: 3px;
                border-radius: 2px;
                background-color: #383838;
                transform: rotateZ(-45deg);
            }
        }
        &__container{
            overflow: hidden;
            width: 100%;
        }
        &__track{
            display: flex;
            transition: transform .3s ease 0s;
        }
        &__item{
            max-width: 284px;
            width: 100%;
            display: flex;
            flex-direction: column;
            padding: 15px 20px 32px;
            border: 1px solid #ECEDEF;
            border-radius: 5px;
            position: relative;
            &:not(:last-child){
                margin-right: 20px;
            }
            &:hover .analog-slider__sale-text{
                position: static;
                opacity: 1;
            }
        }
        &__sale{
            position: absolute;
            top: 15px;
            left: 20px;
            background-color: $red;
            border-radius: 4px;
            padding: 7px;
            color: #fff;
            font-size: count-rem(13);
            font-weight: 500px;
            &-text{
                transition: opacity, position .3s ease 0s;
                opacity: 0;
                position: absolute;
            }
        }
        &__img{
            max-width: 245px;
            max-height: 173px;
            height: 100%;
            & img{
                width: 100%;
                height: 100%;
                object-position: center;
                object-fit: contain;
            }
        }
        &__title{
            margin-top: count-rem(10);
            font-size: count-rem(14);
            font-weight: 400;
            line-height: 120%;
            max-height: 68px;
            height: 100%;
        }
        &__status{
            margin-top: count-rem(26);
            font-size: count-rem(12);
            font-weight: 400;
            line-height: 120%;
            color: $green;
        }
        &__price{
            margin-top: count-rem(6);
            font-size: count-rem(24);
            font-weight: 900;
            line-height: 120%;
            color: #383838;
            &-opt{
                margin-top: count-rem(5);
                font-size: count-rem(14);
                font-weight: 400;
                color: $red;
            }
        }
        &__btn{
            margin-top: count-rem(22);
            padding: count-rem(10) count-rem(19);
            border: none;
            border-radius: 4px;
            background-color: $yellow;
            font-size: count-rem(14);
            font-weight: 700;
            line-height: 120%;
            letter-spacing: 1px;
            text-transform: uppercase;
            transition: background-color .3s ease 0s;
            cursor: pointer;
            &:hover{
                background-color: $darkYellw;
            }
        }
    }
}