// _card.scss
.card{
    padding-top: count-rem(30);
    padding-bottom: count-rem(40);
    &__container{
        display: flex;
        flex-direction: column;
    }
    &__address{
        font-size: count-rem(14);
        font-weight: 400;
        color: #8F8F8F;
        &--dark{
            color: $greyFont;
        }
    }
    &__title{
        margin-top: count-rem(25);
        font-size: count-rem(24);
        font-weight: 500;
        color: $darkFont;
    }
    &__product{
        margin-top: count-rem(20);
        display: flex;
        max-height: 450px;
    }
    &__gallery{
        max-width: 70px;
        width: 100%;
        display: flex;
        flex-direction: column;
        &-item{
            width: 60px;
            height: 60px;
            background-color: transparent;
            border: 1px solid #EEEEEE;
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: border .3s ease 0s;
            &:not(:last-child){
                margin-bottom: 10px;
            }
            & img{
                width: 100%;
                height: 100%;
                object-position: center;
                object-fit: contain;
            }
            &.active{
                border: 1px solid #767676;
            }
        }
    }
    &__img{
        margin-right: count-rem(54);
        max-width: 731px;
        width: 100%;
        display: flex;
        justify-content: center;
    }
    &__descr{
        max-width: 346px;
        width: 100%;
        padding: count-rem(25) count-rem(20);
        display: flex;
        flex-direction: column;
        box-shadow: 0px 4px 5px 0px rgb(248,248,248);
        &-code,
        &-art{
            font-size: count-rem(14);
            font-weight: 400;
            line-height: 200%;
            color: #797979;
            &--value{
                font-weight: 500;
                color: $darkGreyBg;
            }
        }
        &-priceOf{
            margin-top: count-rem(20);
            margin-left: -13px;
            background-color: $lightGreyBg;
            padding: count-rem(8) count-rem(16);
            font-size: count-rem(13);
            font-weight: 400;
            line-height: 120%;
        }
        &-price{
            margin-top: count-rem(47);
            display: flex;
            &-item{
                display: flex;
                flex-direction: column;
                justify-content: flex-end;
                &:first-child{
                    margin-right: count-rem(50);
                }
            }
            &-value{
                font-size: count-rem(32);
                font-weight: 600;
                color: #383838;
                &--small{
                    font-size: count-rem(20);
                    font-weight: 500;
                    color: #949494;
                    position: relative;
                    &::after{
                        position: absolute;
                        top: 60px;
                        left: 50px;
                        content: 'Для получения оптовых цен, добавьте в корзину товаров ещё на 10 000 руб (по оптовым ценам)';
                        color: #fff;
                        font-size: count-rem(16);
                        background-color: #2E2E2E;
                        padding: count-rem(16) count-rem(20);
                        width: 240px;
                        border-radius: 4px;
                        opacity: 0;
                        visibility: hidden;
                        transition: opacity .2s ease 0s;
                    }
                    &:hover{
                        &::after{
                            opacity: 1;
                            visibility: visible;
                        }
                    }
                }
            }
            &-subtext{
                font-size: count-rem(14);
                font-weight: 400;
                color: #383838;
                &--small{
                    font-size: count-rem(14);
                    font-weight: 400;
                    color: #949494;
                }
            }
        }
        &-btn{
            margin-top: count-rem(25);
            padding: count-rem(16) 0 count-rem(14);
            background-color: $yellow;
            border: none;
            border-radius: 4px;
            font-size: count-rem(14);
            font-weight: 600;
            text-transform: uppercase;
            line-height: 100%;
            cursor: pointer;
            transition: background-color .3s ease 0s;
            &:hover{
                background-color: $darkYellw;
            }
        }
        &-availability{
            margin-top: count-rem(25);
            display: flex;
            flex-direction: column;
            &-status{
                margin-bottom: 10px;
                display: flex;
                align-items: center;
                &--text{
                    margin-left: count-rem(12);
                    font-size: count-rem(14);
                    font-weight: 500;
                    line-height: 150%;
                    color: #383838;
                    border-bottom: 1px dashed #383838;
                }
            }
            &-addr{
                font-size: count-rem(13);
                font-weight: 400;
                line-height: 150%;
                color: #383838;
            }
        }
    }
    &__about{
        margin-top: count-rem(120);
        display: flex;
        flex-direction: column;
    }
    &__tabs{
        width: 100%;
        border-bottom: 1px solid #DFDFDF;
        &-list{
            display: flex;
        }
        &-content{
            margin-top: count-rem(30);
        }
    }
    &__tab{
        &-item{
            position: relative;
            padding: count-rem(10) 0;
            &:not(:last-child){
                margin-right: count-rem(40);
            }
            &::after{
                content: '';
                position: absolute;
                bottom: -2px;
                right: 0px;
                height: 3px;
                width: 100%;
                background-color: $yellow;
                opacity: 0;
                transition: opacity .3s ease 0s;
            }
            &.active{
                &::after{
                    opacity: 1;
                }
                & > * {
                    color: #383838;
                }
            }
        }
        &-btn{
            background-color: transparent;
            font-size: count-rem(22);
            font-weight: 500;
            color: #797979;
            cursor: pointer;
            border: none;
            transition: all .3s ease 0s;
            &:hover{
                color: #383838;
            }
        }
        &-content{
            display: none;
            &.active{
                display: flex;
            }
            &-title{
                max-width: 200px;
                width: 100%;
                font-size: count-rem(20);
                font-weight: 500;
                line-height: 100%;
                color: #797979;
            }
            &-descr{
                display: flex;
                flex-direction: column;
            }
            &-text{
                font-size: count-rem(14);
                font-weight: 400;
                line-height: 150%;
                color: $darkFont;
                &:not(:last-child){
                    margin-bottom: count-rem(16);
                }
            }
        }
    }
}

.availability-product{
    width: 100%;
    display: flex;
    flex-direction: column;
    &__titles{
        padding: count-rem(20) count-rem(20);
        display: flex;
    }
    &__title{
        font-size: count-rem(20);
        font-weight: 500;
        color: #797979;
    }
    &__address{
        max-width: 540px;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content:center;
        &-station{
            margin-bottom: count-rem(16);
            font-size: count-rem(18);
            font-weight: 500;
        }
    }
    &__timetable{
        max-width: 240px;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content:center;
    }
    &__availability{
        max-width: 180px;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content:center;
        &-status{
            color: $green;
        }
        & .dont{
            color: #797979;
        }
    }
    &__phone{
        max-width: 230px;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content:center;
    }
    &__item{
        padding: count-rem(25) count-rem(20);
        display: flex;
        color: #0B0B0B;
        font-size: count-rem(16);
        font-weight: 400;
        line-height: 150%;
        &:nth-child(2n){
            background-color: $lightGreyBg;
        }
    }
}