.header{
    box-shadow: 0px 4px 5px 0px rgb(248,248,248);
}
.header-top{
    background-color: $darkGreyBg;
    padding: count-rem(15) 0px;
    &__container{
        display: flex;
        justify-content: space-between;
    }
    &__list{
        display: flex;
        font-size: count-rem(14);
        line-height: 150%;
        font-weight: 400;
        &-social{
            margin-right: count-rem(27);
        }
    }
    &__item{
        display: flex;
        &:not(:last-child){
            margin-right: count-rem(20);
        }
        &--border{
            width: 1px;
            height: 100%;
            background-color: $lightFont;
        }
    }
    &__link{
        transition: all .3s ease 0s;
        color: $lightFont;
        &:hover{
            color: #fff;
        }
        &--mail,
        &--tel{
            font-weight: 500;
            color: #fff;
        }
        &--social{
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
    &__contacts{
        display: flex;
        align-items: center;
    }
    &__callback{
        margin-right: count-rem(27);
        font-size: count-rem(14);
        font-weight: 400;
        color: $yellow;
        background-color: transparent;
        border: 1px solid transparent;
        border-bottom: 1px dashed $yellow;
        cursor: pointer;
        transition: all .3s ease 0s;
        &:hover{
            color: $darkYellw;
        }
    }
}

.control{
    padding: count-rem(22) 0;
    &__container{
        display: flex;
        justify-content: space-between;
    }
}
.logo{
    &__link{
        display: flex;
        align-items: center;
    }
    &__img{
        width: 44px;
        height: 44px;
        margin-right: 17px;
        background: url('../img/logo.png');
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        &--large{
            width: 115px;
            height: 20px;
            background: url('../img/logo__text.png');
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
        }
    }
}
.search{
    max-height: 48px;
    width: 100%;
    margin-left: count-rem(28);
    margin-right: count-rem(15);
    &__form{
        height: 100%;
        display: flex;
        align-items: center;
    }
    &__container{
        height: 100%;
        width: 100%;
        display: flex;
        border: 1px solid #ECEDEF;
        border-right: none;
        border-top-left-radius: 3px;
        border-bottom-left-radius: 3px;
        padding: 0 count-rem(20);
    }
    &__input{
        height: 100%;
        width: 100%;
        border: 1px solid transparent;
        font-size: count-rem(14);
        outline: transparent;
    }
    &__btn{
        height: 100%;
        max-width: 48px;
        width: 100%;
        border: 1px solid $yellow;
        border-top-right-radius: 3px;
        border-bottom-right-radius: 3px;
        background-color: $yellow;
        cursor: pointer;
        transition: all .3s ease 0s;
        &:hover{
            background-color: $darkYellw;
        }
    }
}
.control-btn{
    &__btn{
        height: 100%;
        width: 180px;
        background-color: $lightGreyBg;
        border: 1px solid $lightGreyBg;
        border-radius: 4px;
        margin-left: count-rem(15);
        display: flex;
        cursor: pointer;
        transition: all .3s ease 0s;
        &:hover{
            background-color: #EFEFEF;
        }
    }
    &__img{
        width: 48px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-right: 1px solid #E4E4E4;
        &.backet{
            position: relative;
            &::after{
                position: absolute;
                content: '';
                background-color: $red;
                width: 9px;
                height: 9px;
                border-radius: 100%;
                top: 5px;
                right: 5px;
            }
        }
    }
    &__descr{
        padding-left: 10px;
        font-size: count-rem(13);
        font-weight: 500;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
    }
    &__text--light{
        color: #ACACAC;
    }
}

.nav{
    &__container{
        display: flex;
        align-items: center;
        padding-top: count-rem(22);
        padding-bottom: count-rem(22);
    }
    &__list{
        padding-left: count-rem(24);
        display: flex;
        border-left: 1px solid $lightFont;
    }
    &__item{
        &:not(:last-child){
            margin-right: count-rem(20);
        }
    }
    &__btn{
        padding: count-rem(5) 0;
        line-height: 150%;
        font-size: count-rem(14);
        font-weight: 400;
        text-transform: uppercase;
        color: $lightFont;
        cursor: pointer;
        background-color: transparent;
        border: 1px solid transparent;
        transition: all .3s ease 0s;
        &:hover{
            color: $darkGreyBg;
        }
        &--bold{
            font-weight: 600;
            color: $darkGreyBg;
            margin-right: count-rem(25);
        }
    }
}