.footer{
    padding: count-rem(67) 0;
    background-color: $yellow;
    &__container{
        display: flex;
        align-items: center;
    }
    &__img{
        margin-right: count-rem(42);
        width: 60px;
        height: 60px;
        & > img {
            width: 100%;
            height: 100%;
            object-position: center;
            object-fit: cover;
        }
    }
    &__title{
        max-width: 310px;
        width: 100%;
        font-size: count-rem(32);
        font-weight: 500;
        line-height: 110%;
        color: $darkGreyBg;
    }
    &__text{
        margin-right: count-rem(76);
        max-width: 264px;
        width: 100%;
        font-size: count-rem(16);
        font-weight: 500;
        line-height: 150%;
        color: $darkGreyBg;
    }
}
.subscribe{
    display: flex;
    align-items: center;
    &__inpt{
        margin-right: count-rem(20);
        max-width: 260px;
        width: 100%;
        padding: count-rem(10) count-rem(19);
        background-color: transparent;
        border: 1px solid $darkGreyBg;
        border-radius: 4px;
        font-size: count-rem(14);
        font-weight: 500;
        color: $darkGreyBg;
    }
    &__btn{
        padding: count-rem(14) count-rem(30);
        font-size: count-rem(14);
        font-weight: 600;
        color: $yellow;
        background-color: $darkGreyBg;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        transition: all .3s ease 0s;
        &:hover{
            color: #fff;
        }
    }
}